import { Header } from "../components/Header";
import styles from "./Lk.module.css";
import {
  useUserEmail,
  useSetStatus,
  useClearCart,
  usePurchasesInfo,
  usePurchaseList,
} from "../store/store";
import { useAuth } from "../hooks/useAuth";
import { Footer } from "../components/Footer";
import { Cart } from "../components/Cart";
import { useNavigate } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import { Menu } from "../components/Menu";

export function Lk() {
  const email = useUserEmail();
  const navigate = useNavigate();
  const setStatus = useSetStatus();
  const clearCart = useClearCart();
  const purchaseInfo = usePurchasesInfo();
  const purchasesList = usePurchaseList();
  const { extendCouse } = useAuth();

  const extendCouseHandler = async (id) => {
    const res = await extendCouse(id);
    window.location.assign(res);
  };

  const pluralRu = (n, one, two, five) => {
    const mod10 = n % 10;
    const mod100 = n % 100;
    if (mod10 === 1 && mod100 !== 11) return one;
    if (mod10 >= 2 && mod10 <= 4 && (mod100 < 10 || mod100 >= 20)) return two;
    return five;
  };

  const getLeftText = (id) => {
    const item = purchasesList.find((p) => p.ProductID === id);
    if (!item) return null;

    const days = Number(item.DifferenceTime.Days ?? 0);
    const hours = Number(item.DifferenceTime.Hours ?? 0);

    if (days > 0) {
      const d = days + (hours > 0 ? 1 : 0);
      return `${d} ${pluralRu(d, "", "", "")}`;
    }

    const h = Math.max(1, hours);
    return `${h} ${pluralRu(h, "", "", "")}`;
  };

  return (
    <div className={styles["lk__wrapper"]}>
      <Header userButtonVariant="modal" />
      <div className={styles["lk__inside-wrapper"]}>
        <div className={styles["lk__utils"]}>
          <div>
            <h2 className={styles["lk__title"]}> </h2>
            <p className={styles["lk__paragraph"]}>: {email}</p>
          </div>
          <button
            onClick={() => {
              setStatus("guest");
              localStorage.removeItem("token");
              navigate("/");
              window.scrollTo(0, 0);
              clearCart();
            }}
            className={styles["exit_button"]}
          >
              
          </button>
        </div>
        <p className={styles["lk__my-trainig"]}> </p>
        {Object.keys(purchaseInfo).length > 0 ? (
          <div className={styles["lk__trainings"]}>
            {purchaseInfo[1] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img src="/images/training1_mini.png" alt="" />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                        - <br />
                       
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>  {getLeftText(1)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(1)}
                      >
                         
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                      
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[2] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img src="/images/training2_mini.png" alt="" />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                         <br />
                      full body
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>  {getLeftText(2)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(2)}
                      >
                         
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                      
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[3] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img src="/images/training3_mini.png" alt="" />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                        - <br />
                       
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>  {getLeftText(3)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(3)}
                      >
                         
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                      
                  </button>
                </div>
              </div>
            )}
            {purchaseInfo[4] != null && (
              <div className={styles["lk__trainings-element"]}>
                <img src="/images/training4_mini.png" alt="" />
                <div className={styles["lk__trainings-element-wrapper"]}>
                  <div
                    className={styles["lk__trainings-element-second-wrapper"]}
                  >
                    <p>
                        - <br />
                       
                    </p>
                    <div className={styles["lk__trainings-element-time"]}>
                      <p>  {getLeftText(4)}</p>
                      <button
                        className={styles["lk_trainings-element-extend-btn"]}
                        onClick={() => extendCouseHandler(4)}
                      >
                         
                      </button>
                    </div>
                  </div>
                  <button
                    className={styles["lk__trainings-element-proceed-btn"]}
                  >
                      
                  </button>
                </div>
              </div>
            )}
          </div>
        ) : (
          <div className={styles["empty_cart"]}>
            <h2>...   ...</h2>
            <p>
              ,    .   
              !
            </p>
            <button onClick={() => navigate("/#training")}>
              <HashLink smooth to="/#training">
                 
              </HashLink>
              <svg
                width="21"
                height="19"
                viewBox="0 0 21 19"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M19.3862 0.814178C19.3422 0.314411 18.8232 -0.0481573 18.2269 0.00435998L8.50958 0.860179C7.91328 0.912696 7.46557 1.36041 7.50958 1.86018C7.5536 2.35995 8.07267 2.72251 8.66897 2.67L17.3066 1.90927L17.9441 9.14854C17.9881 9.64831 18.5072 10.0109 19.1035 9.95836C19.6998 9.90584 20.1475 9.45813 20.1035 8.95836L19.3862 0.814178ZM0.819336 18.3965L1.63915 18.9691L19.1264 1.4819L18.3066 0.909269L17.4867 0.336641L-0.000479519 17.8239L0.819336 18.3965Z"
                  fill="#FBE0F3"
                />
              </svg>
            </button>
          </div>
        )}
      </div>
      <Footer />
      <Cart />
      <Menu />
    </div>
  );
}
