import "./App.css";
import { useEffect } from "react";
import { Home } from "./pages/Home";
import { myStore } from "./store/store";
import { Routes, Route } from "react-router-dom";
import { Lk } from "./pages/Lk";
import { ModalLogin } from "./pages/ModalLogin";
import { ModalRegister } from "./pages/ModalRegister";
import { RecoverForm } from "./pages/RecoverForm";
import { Recover } from "./pages/Recover";
import { Calc } from "./pages/Calc";
import { useCartStatus, useMenuIsOpen } from "./store/store";
import Lenis from "lenis";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

function App() {
  const check = myStore((s) => s.check);
  const cartStatus = useCartStatus();
  const menuStatus = useMenuIsOpen();

  useEffect(() => {
    const lenis = new Lenis({
      lerp: 0.1,
      wheelMultiplier: 1.25,
      prevent: (node) => node.closest('[data-lenis-prevent="true"]'),
    });

    const onTick = (time) => {
      lenis.raf(time * 1000);
    };

    lenis.on("scroll", ScrollTrigger.update);
    gsap.ticker.add(onTick);
    gsap.ticker.lagSmoothing(0);

    return () => {
      gsap.ticker.remove(onTick);
      lenis.destroy();
    };
  }, []);

  useEffect(() => {
    check();
  }, [check]);

  useEffect(() => {
    const isScrollLocked = menuStatus || cartStatus;
    let timer;

    const scrollbarWidth =
      window.innerWidth - document.documentElement.clientWidth;

    if (isScrollLocked) {
      document.documentElement.style.overflow = "hidden";

      if (cartStatus) {
        document.body.style.paddingRight = `${scrollbarWidth}px`;
      } else {
        document.body.style.paddingRight = "0px";
      }
    } else {
      timer = setTimeout(() => {
        document.documentElement.style.overflow = "auto";
        document.body.style.paddingRight = "0px";
      }, 50);
    }

    return () => {
      if (timer) {
        clearTimeout(timer);
      }
    };
  }, [menuStatus, cartStatus]);

  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/login" element={<ModalLogin />} />
      <Route path="/profile" element={<Lk />} />
      <Route path="/register" element={<ModalRegister />} />
      <Route path="/RecoverForm" element={<RecoverForm />} />
      <Route path="/recover" element={<Recover />} />
      <Route path="/calc" element={<Calc />} />
    </Routes>
  );
}

export default App;
